.App {
}

.logo-container {
  .App-logo {
    display: block;
    height: 40vmin;
    border-radius: 8px;

    &:hover {
      box-shadow: 0 0 2px 1px rgba(0, 140, 186, 0.5);
    }
  }
}

.info-text {
  $padding: 15%;
  padding-left: $padding;
  padding-right: $padding;

  text-align: center;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.app-card {
  margin: 10px 0px;
}

.app-header {
  font-size: 24px;
  font-weight: bold;
  border-bottom: 1px solid #e1e8ed;
  padding-bottom: 5px;
  margin-bottom: 10px;
}

.services {
  $padding: 15%;
  padding-left: $padding;
  padding-right: $padding;
}
